.sticky-header {
    position: sticky;
    top: 0;
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
    z-index: 100; /* Adjust the z-index as needed */
}

nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
}
  
nav li {
    margin-right: 20px;
    display: inline;
}

nav a {
    color: #fff;
    text-decoration: none;
    font-weight: bold;
    font-size: 16px;
}
  
/* Add more styles as needed */
  