.container1 {
    display: flex;
    padding-top: 5%;
  }
  
  .left-column1 {
    flex: 1.25;
    position: relative;
    background-color: #ffffff;
  }
  
  .background-image1 {
    background-image: url('../../../public/Premium\ Photo\ _\ Taiwan\ milk\ tea\ with\ bubble.jpeg'); /* Replace with your image path */
    background-size: fixed;
    background-attachment: fixed;
    background-repeat:repeat-x;
    background-position: right;
  }
  .background-image1 h1{
    padding: 40% 12% 40% 12%;
    color:#000000;
  }
  
  .right-column1 {
    flex: 0.75;
    padding:10% 5% 5% 5%;
    background-color: #fff;
  }
  .right-column1 h2 {
    padding: 0% 0% 5% 0%;
  }
  @media (max-width: 768px) {
    .container1 {
      flex-direction:column-reverse; /* Stack columns vertically */
      padding: 5%;
    }
  
    .left-column1 {
      flex: 1;
    }
  
    .background-image1{
      background-image: url('../../../public/Premium\ Photo\ _\ Taiwan\ milk\ tea\ with\ bubble.jpeg'); /* Replace with your image path */
      background-size: cover;
      background-attachment:initial;
    }
    .background-image1 h1 {
      padding: 10% 5% 10% 0;
      font-size: 24px; /* Adjust the font size for small screens */
    }
  
    .right-column1 {
      flex: 1;
      padding: 5%;
    }
  
    .right-column1 h2 {
      padding: 0% 0% 3% 0%;
      font-size: 20px; /* Adjust the font size for small screens */
    }
  }