/* TwoColumnLayout.css */

.container {
    display: flex;
    padding-top: 5%;
  }
  
  .left-column {
    flex: 1.25;
    position: relative;
    background-color: #ffffff;
  }
  
  .background-image {
    background-image: url('../../../public/Emma\'s\ family\ feasts_\ Cornish\ pasties.jpeg'); /* Replace with your image path */
    background-size: fixed;
    background-attachment: fixed;
    background-repeat:repeat-x;
    background-position: left;
  }
  .background-image h1{
    padding: 30% 12% 30% 12%;
    color:#000000;
  }
  
  .right-column {
    flex: 0.75;
    padding:10% 5% 5% 5%;
    background-color: #fff;
  }
  .right-column h2 {
    padding: 0% 0% 5% 0%;
  }
  @media (max-width: 768px) {
    .container {
      flex-direction: column; /* Stack columns vertically */
      padding: 5%;
    }
  
    .left-column {
      flex: 1;
    }
  
    .background-image{
      background-image: url('../../../public/Emma\'s\ family\ feasts_\ Cornish\ pasties.jpeg'); /* Replace with your image path */
      background-size: cover;
      background-attachment:initial;
    }
    .background-image h1 {
      padding: 10% 5% 10% 0;
      font-size: 24px; /* Adjust the font size for small screens */
    }
  
    .right-column {
      flex: 1;
      padding: 5%;
    }
  
    .right-column h2 {
      padding: 0% 0% 3% 0%;
      font-size: 20px; /* Adjust the font size for small screens */
    }
  }
  