container2 {
    display: flex;
    padding-top: 5%;
  }
  
  .left-column2 {
    flex: 1.25;
    position: relative;
    background-color: #ffffff;
  }
  
  .background-image2 {
    background-image: url('../../../public/Rocked\ It.jpeg'); /* Replace with your image path */
    background-size: fixed;
    background-attachment: fixed;
    background-repeat:repeat-x;
    background-position: left;
  }
  .background-image2 h1{
    padding: 30% 12% 30% 12%;
    color:#000000;
  }
  
  .right-column2 {
    flex: 0.75;
    padding:10% 5% 5% 5%;
    background-color: #fff;
  }
  .right-column2 h2 {
    padding: 0% 0% 5% 0%;
  }
  @media (max-width: 768px) {
    .container2 {
      flex-direction: column; /* Stack columns vertically */
      padding: 5%;
    }
  
    .left-column2 {
      flex: 1;
    }
  
    .right-column2 {
      flex: 1;
      padding: 5%;
    }
  
    .right-column2 h2 {
      padding: 0% 0% 3% 0%;
      font-size: 20px; /* Adjust the font size for small screens */
    }
  }
  